.header-container-main {
    background-color: white;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1000;
    box-shadow: 0 5px 15px 1px rgba(0, 0, 0, 0.1);
  }
  .header-container-main .header-container {
    padding: 5px;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    background-color: white;
  }
  .header-container-main .header-container .main-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
  }
  .header-container-main .header-container .main-header .image-wrapper .logo {
    padding: 18px 0;
  }
  @media (max-width: 1239px) {
    .header-container-main .header-container .main-header .image-wrapper .logo {
      padding-left: 20px;
    }
  }
  @media (max-width: 1239px) {
    .header-container-main .header-container .main-header .main-navigation {
      display: none;
    }
  }
  .header-container-main .header-container .main-header .main-navigation ul {
    display: flex;
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu {
    padding-left: 0;
    margin: 0;
    align-items: center;
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu li:nth-child(1) {
    padding-left: 0;
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu li:nth-child(9) {
    padding-right: 0;
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu li:last-child {
    padding-right: 0;
    padding-left: 14px;
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu .button-skambinti {
    color: white;
    font-size: 11px;
    font-weight: 600;
    font-family: Bw Nista Int, Arial, sans-serif;
    text-decoration: none;
    letter-spacing: 1px;
    background-color: blue;
    border: 1px solid blue;
    border-radius: 40px;
    padding: 14px 40px 12px 40px;
    margin-left: 20px;
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu .button-skambinti:hover {
    background-color: rgb(59, 59, 250);
    border: 1px solid rgb(59, 59, 250);
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu .nav-menu-item {
    list-style-type: none;
    padding: 30px 14px;
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu .nav-menu-item .menu-link {
    text-decoration: none;
    color: black;
    font-family: Bw Nista Int, Arial, sans-serif;
    font-size: 14px;
    font-weight: 700;
  }
  .header-container-main .header-container .main-header .main-navigation .main-menu .nav-menu-item .menu-link:hover {
    color: rgba(0, 0, 0, 0.7);
  }